.container {
    padding: 0 2rem;
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
}

.wrapper {
    display: flex;
    justify-content: center;
    align-items: baseline;
    position: relative;
}

.wrapper>img {
    width: 30rem;
    position: absolute;
    bottom: 0;
}

.blueCircle {
    width: 30rem;
    height: 30rem;
    border-radius: 50%;
    z-index: -99;
    position: absolute;
    bottom: 0;
    background: linear-gradient(135.74deg, #0bb5ff 14.09%, #6dd3fe 83.81%);
}

.cart2 {
    position: absolute;
    bottom: 25%;
    right: 5%;
    display: flex;
    gap: 1rem;
    align-items: center;
}

.cart2>svg {
    width: 30px;
    height: 30px;
    background-color: white;
    padding: 10px;
    border-radius: 50%;
    border: 6px solid var(--black);
}

.signup {
    display: flex;
    gap: 1rem;
    align-items: center;
    background-color: white;
    padding: 10px;
    font-size: 0.8rem;
    border-radius: 15px;
    box-shadow: var(--shadow1);
}

.signup>:first-child {
    display: block;
    width: 6rem;
}

.signup>:nth-child(2) {
    border-radius: 50%;
    border: 1px solid skyblue;
    display: flex;
    width: 20px;
    height: 20px;
    padding: 5px;
    align-items: center;
    justify-content: center;
}


.h_sides {
    display: grid;
    grid-template-rows: 1fr 1fr;
}

.text1 {
    text-transform: uppercase;
    font-size: 1.5rem;
    width: min-content;
    display: block;
    font-weight: 600;
}

.text2 {
    display: flex;
    flex-direction: column;
    width: min-content;
    color: black;
}

.text2>:first-child {
    font-weight: 800;
    font-size: 2.5rem;
}

.text2>:nth-child(2) {
    display: block;
}

.traffic {
    display: flex;
    flex-direction: column;
    text-align: right;
}

.traffic>:first-child {
    font-weight: 800;
    font-size: 2.5rem;
}

.customers {
    display: flex;
    flex-direction: column;
    text-align: right;
}

.customers> :first-child {
    font-weight: 800;
    font-size: 2.5rem;
}

@media screen and (max-width: 850px) {

    .text1,
    .text2>span:nth-of-type(2) {
        font-size: 0.9rem;
        text-align: justify;
    }

    .text2>span:nth-of-type(1),
    .traffic>span:nth-child(1),
    .customers>span:nth-child(1) {
        font-size: 1.5rem;
        text-align: left;
    }

    .blueCircle {
        position: absolute;
        width: 70%;
        height: 100%;
    }

    .wrapper>img {
        width: 70%;
        height: 100%;
    }

    .container {
        grid-template-areas:
            'left center center'
            'right right right';

    }

    .container>:first-child {
        grid-area: left;
        grid-template-rows: none;
        gap: 8rem;
    }

    .container>:nth-child(2) {
        grid-area: center;
    }

    .container>:nth-child(3) {
        grid-area: right;
        display: flex;
        justify-content: space-around;
        margin: 2rem 0rem;
    }

    .cart2 {
        right: 2rem;
        bottom: 2rem;
    }
}

@media screen and (max-width: 640px) {
    .blueCircle {
        top: 6rem;
        width: 16rem;
        height: 72%;
    }

    .wrapper>img {
        width: 12rem;
        height: 15rem;
    }

    .cart2 {
        transform: scale(0.7);
        right: 2rem;
        bottom: -1rem;
    }
}